* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
}

body {
  min-height: 100vh;
  display: grid;
  justify-items: center;
}

html {
  scroll-behavior: smooth;
}

img,
svg {
  max-width: 100%;
}

a,
a:hover,
a:visited,
a:focus {
  text-decoration: none;
}

.module {
  width: 1440px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 5%;
  align-items: center;
  align-content: start;
  margin-top: 96px;
}

.module__content {
  justify-self: left;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 380px;
  height: 250px;
}

.module__image {
  justify-self: right;
  max-width: 505px;
  height: 300px;
}

.module__button {
  cursor: pointer;
  width: 128px;
  height: 48px;
  padding: 16px 40px 16px 40px;
  background-color: #e4c1ae;
  border: none;
  border-radius: 32px;
  font-family: Roboto;
  font-size: 1rem;
  color: #fff;
  text-align: center;
}

.module__title {
  font-family: Roboto;
  font-style: normal;
  font-size: 2.625rem;
  font-weight: 700;
  color: #121212;
}

.module__text {
  font-family: Roboto;
  font-style: normal;
  font-size: 1rem;
  font-weight: 300;
  color: #121212;
  text-align: justify;
  line-height: 150%;
}

.module__popup {
  display: none;
  place-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: rgba(18, 18, 18, 0.5);
}

.module__content--popup {
  justify-self: center;
  max-width: 920px;
  width: 920px;
  height: 210px;
  background-color: #fff;
}

.module__close-button {
  cursor: pointer;
  position: relative;
  top: -164px;
  left: 874px;
  width: fit-content;
  height: fit-content;
}

.semibold {
  font-weight: 600;
}